/* src/components/Projects/Projects.css */
.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f7f7f7;
  text-align: center;
  padding: 20px;
}

.animation-container {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.circle {
  width: 20px;
  height: 20px;
  margin: 5px;
  background-color: #282c34;
  border-radius: 50%;
  animation: bounce 0.6s infinite alternate;
}

.circle:nth-child(2) {
  animation-delay: 0.2s;
}

.circle:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  to {
    transform: translateY(-20px);
  }
}

.no-projects-message {
  font-size: 1.2rem;
  color: #282c34;
}

.contact-link {
  margin-top: 20px;
}

.contact-link-button {
  display: inline-block;
  padding: 10px 20px;
  color: white;
  background-color: #282c34;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact-link-button:hover {
  background-color: #61dafb;
  transform: scale(1.05);
}
