/* src/components/Contact/Contact.css */
.contact {
  padding: 50px;
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.contact h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #282c34;
}

.contact-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact-details,
.contact-form {
  width: 45%;
  margin-bottom: 30px;
}

.contact-details h3,
.contact-form h3 {
  margin-bottom: 20px;
  color: #282c34;
}

.contact-details p,
.contact-details .social-links a {
  margin: 10px 0;
  color: #282c34;
  display: flex;
  align-items: center;
}

.contact-details p svg,
.contact-details .social-links a svg {
  margin-right: 10px;
}

.social-links {
  display: flex;
  justify-content: center;
}

.social-links a {
  margin: 0 10px;
  font-size: 1.5rem;
  color: #282c34;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: #61dafb;
}

.contact-form label {
  display: block;
  margin-bottom: 15px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  padding: 10px 20px;
  color: white;
  background-color: #282c34;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact-form button:hover {
  background-color: #61dafb;
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .contact-details,
  .contact-form {
    width: 100%;
  }
}
