/* src/components/Home/Home.css */
.home {
  text-align: center;
  padding: 50px 20px;
}

.intro {
  margin-bottom: 30px;
}

.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
}

.profile-image {
  flex: 1;
}

.profile-image img {
  width: 200px;
  border-radius: 12.5px;
}

.typed-container {
  flex: 2;
  height: 40px; /* Adjust this height based on the font size and line height */
  display: flex;
  align-items: center;
  justify-content: center;
}

.typed-text {
  font-size: 2.5rem;
}

.call-to-action {
  margin-top: 20px;
}

.call-to-action a {
  text-decoration: none;
  color: #61dafb;
  font-size: 1.2rem;
}

@media (max-width: 768px) {
  .flex-container {
    flex-direction: column;
  }

  .profile-image img {
    width: 100px;
  }

  .typed-text {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .typed-text {
    font-size: 1.2rem;
  }

  .call-to-action a {
    font-size: 1rem;
  }
}
