/* src/components/Header/Header.css */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #282c34;
  padding: 20px;
  color: white;
  transition: background-color 0.5s ease, color 0.5s ease;
}

.logo h1 {
  margin: 0;
  font-size: 1.5rem;
  color: white;
  text-decoration: none;
}

.logo a {
  color: inherit;
  text-decoration: none;
}
nav ul {
  list-style-type: none;
  padding: 0;
  display: flex;
}

nav li {
  margin: 0 10px;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

nav a.active {
  font-weight: bold;
  border-bottom: 2px solid #61dafb;
}

nav a:hover {
  /* Remove default underline and add your desired hover effect */
  text-decoration: none;
}

.theme-toggle,
.menu-toggle {
  background: none;
  border: none;
  cursor: pointer;
  color: white;
  font-size: 1.5rem;
}

.theme-toggle:focus,
.menu-toggle:focus {
  outline: none;
}

button:hover {
  color: #61dafb;
}

.dark-mode {
  background-color: #121212;
  color: white;
}

@media (max-width: 768px) {
  header {
    flex-direction: column;
    text-align: center;
  }

  .logo h1 {
    font-size: 1.25rem;
  }

  nav ul {
    flex-direction: column;
  }

  nav.open ul {
    display: flex;
  }

  nav li {
    margin: 10px 0;
  }

  nav a {
    font-size: 0.9rem;
  }

  .theme-toggle,
  .menu-toggle {
    font-size: 1.25rem;
  }
}

@media (max-width: 480px) {
  .logo h1 {
    font-size: 1rem;
  }

  nav a {
    font-size: 0.8rem;
  }

  .theme-toggle,
  .menu-toggle {
    font-size: 1rem;
  }
}
