/* src/components/About/About.css */
* {
  box-sizing: border-box;
}
.about {
  padding: 50px;
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.about h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #282c34; /* Updated color */
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.education,
.skills,
.interests {
  text-align: center;
  width: 30%; /* Adjust the width based on your preference */
  margin-bottom: 30px;
  /* Smooth transition for the border effect */
}

h3 {
  margin-bottom: 10px;
  color: #282c34; /* Updated color */
}

.skills ul {
  list-style-type: none;
  padding: 0;
}

.skills li {
  margin-bottom: 5px;
}

.interests p {
  line-height: 1.6;
}

/* Styling for icons */
.about h3 svg {
  margin-right: 10px;
  color: #282c34; /* Updated color */
}

/* Hover effect for sections */
.education:hover,
.skills:hover,
.interests:hover {
  border: 2px solid #282c34; /* Border color */
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Border radius to match the container */
}

@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
  }

  .education,
  .skills,
  .interests {
    width: 100%;
  }
}

/* Add this to src/components/About/About.css */
.projects-link {
  text-align: center;
  margin-top: 20px;
}

.projects-link-button {
  display: inline-block;
  padding: 10px 20px;
  color: white;
  background-color: #282c34;
  border: none;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.projects-link-button:hover {
  background-color: #61dafb;
  transform: scale(1.05);
}
